.user-profile {
  white-space: nowrap;
  background: var(--navigation-client-background-color);
  color: var(--navigation-client-font-color);
  border-bottom-style: solid;
  border-bottom-width: var(--navigation-client-border-width);
  border-color: var(--navigation-client-border-color);
}

.sidebar-brand {
  color: var(--navigation-font-color);
  border-color: var(--navigation-border-color);
  border-width: var(--navigation-border-width);
  background-color: var(--navigation-logo-background-color);
  padding: var(--padding) 0;
  img {
    width: auto;
    max-width: 120px;
  }
}
